/* src/styles/global.css */
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
@import 'tw-animate-css';

/* Pega aquí las variables y custom properties de la documentación */
@custom-variant dark (&:is(.dark *));
:root {
  /* ...todas las variables que copiaste... */
}

/* Diseño base */
body {
  font-family: 'Poppins', sans-serif;
  background-color: #f4f7fc;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

h1,
h2 {
  font-family: 'Roboto', sans-serif;
  color: #333;
  text-align: center;
}

/* Estilo de formulario */
input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

button {
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

button:disabled {
  background-color: #ccc;
}

/* Estilo de lista de reparaciones */
div.reparacion {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

button:hover {
  background-color: #0056b3;
}

button:disabled:hover {
  background-color: #ccc;
}

/* Estilo de los iconos */
i {
  margin-right: 10px;
}

/* Estilo para los botones de acción */
button {
  display: flex;
  align-items: center;
}

button + button {
  margin-left: 10px;
}
